import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { decreaseAmount } from "./actions";
import data from "./data";
import { useEffect, useState } from "react";
import Editions from "../../components/editions/Editions";
import { EDITION_TYPE } from "./constants";

const EditionContainer = ({
  enoughPledged,
  edition,
  modalMode,
  tempPledgeValue,
  notEnoughPledged,
}) => {
  const dispatch = useDispatch();

  function enoughWasPledged(e) {
    e.preventDefault();
    enoughPledged();

    edition !== EDITION_TYPE.NOREWARD && dispatch(decreaseAmount(edition))}
  

  const [editionType, setIsEditionType] = useState();

  const amount = useSelector((state) => state.editionCount[edition]);

  useEffect(() => {
    const editionTypeHandler = (edition) => {
      switch (edition) {
        case EDITION_TYPE.NOREWARD:
          return setIsEditionType(data.noReward);
        case EDITION_TYPE.BAMBOO:
          return setIsEditionType(data.bamboo);
          case EDITION_TYPE.BLACK:
          return setIsEditionType(data.black);
        case EDITION_TYPE.SPECIAL:
          return setIsEditionType(data.special);
        default:
          return setIsEditionType();
      }
    };

    editionTypeHandler(edition);
  }, []);



  return (
    <div>
      <Editions
        title={editionType && editionType.title}
        setEditionID={editionType && editionType.editionId}
        editionMinAmount={editionType && editionType.minAmount}
        info={editionType && editionType.info}
        startCount={amount}
        isModalMode={modalMode}
        text={edition !== EDITION_TYPE.NOREWARD && "left"}
          submit={
          editionType && editionType.minAmount < tempPledgeValue
            ? enoughWasPledged
            : notEnoughPledged
        }
      />
    </div>
  );
};

export default EditionContainer;
