import React from "react";
import { useState } from "react";
import classes from "./Editions.module.css";
import Button from "../buttons/Button";
import PledgeAreaContainer from "../../containers/pledgeArea";

function Editions({
  startCount,
  modalMode,
  editionMinAmount,
  title,
  info,
  text,
  submit,
 
}) {
  const [isModalMode, setModalMode] = useState(modalMode);


  function setAboutMode() {
    setModalMode(true);
  }

  return (
    <div
      className={
        startCount === 0
          ? `${classes.wrapper} ${classes.overlay}`
          : `${classes.wrapper} ${classes.active}`
      }
    >
      <div className={classes.container}>
        <div className={classes.titlePledgeContainer}>
          <div className={classes.title}> {title} </div>
          <div className={classes.pledge__text}>
            Pledge â‚¬{editionMinAmount} or more
          </div>
        </div>
        <div className={classes.description}>{info}</div>
        <div className={classes.amountAndButton}>
          <div className={classes.amount}>
            {startCount} <span className={classes.left}>{text}</span>
          </div>
          {!isModalMode && (
            <Button text={"Select Reward"} click={setAboutMode} />
          )}
        </div>
      </div>

         {(title === "Pledge with no reward" || startCount > 0) && isModalMode && (
        <PledgeAreaContainer
          submit={submit}
          
        />
      )}
    </div>
  );
}

export default Editions;
