import React from "react";
import classes from "./pledgeModal.module.css";
import { ReactComponent as SuccessIcon } from "./assets/icon-check.svg";
import Button from "../buttons/Button";



function PledgeModal({title, msg, close}) {



     return (
      <div className={classes.container}>
        <SuccessIcon />
        <div className={classes.title}> {title}</div>
        <div className={classes.msg}>{msg}
        </div>
  
        <Button smallButton="true" text="Got it!" click={close} />
      </div>
    );
  }
  
  export default PledgeModal;
