import {
  DECREASE_BAMBOO_EDITION,
  DECREASE_BLACK_EDITION,
  DECREASE_SPECIAL_EDITION,
} from "../constants";

const initialState = {
  bamboo: 10,
  black: 10,
  special: 10,
};

export default function editionCountReducer(state = initialState, action) {
  switch (action.type) {
    case DECREASE_BAMBOO_EDITION:
      return { ...state, bamboo: state.bamboo - 1 };
         case DECREASE_BLACK_EDITION:
      return { ...state, black: state.black - 1 };
          case DECREASE_SPECIAL_EDITION:
      return { ...state, specialEdition: state.specialEdition - 1 };
    default:
      return state;
  }
}
