import {
  SET_PLEDGE_VALUE,
  RESET_PLEDGE_VALUE,
  UPDATE_TOTAL_PLEDGES,
  INCREASE_BACKERS,
  SET_ENOUGH_PLEDGED,
  SET_PLEDGE_SUBMITTED,
  RESET_ENOUGH_PLEDGED,
  RESET_PLEDGE_SUBMITTED,
  SET_TEMP_PLEDGE_VALUE,
  RESET_TEMP_PLEDGE_VALUE,
} from "../constants";

const pledgesInitialState = {
  totalPledges: 75000,
  numOfBackers: 5999,
  progressBar: 85,
  pledgeValue: 0,
  tempPledgeValue: 0,
  enoughPledged: false,
  pledgeSubmitted: false,
};

export default function pledgeValuesReducer(
  state = pledgesInitialState,
  action
) {
  switch (action.type) {
    case SET_PLEDGE_VALUE:
      return { ...state, pledgeValue: parseInt(action.data) };
    case RESET_PLEDGE_VALUE:
      return { ...state, pledgeValue: 0 };
    case UPDATE_TOTAL_PLEDGES:
      return { ...state, totalPledges: state.totalPledges + action.data };
    case INCREASE_BACKERS:
      return { ...state, numOfBackers: state.numOfBackers + 1 };
    case SET_ENOUGH_PLEDGED:
      return { ...state, enoughPledged: true };
    case SET_PLEDGE_SUBMITTED:
      return { ...state, pledgeSubmitted: true };
    case RESET_PLEDGE_SUBMITTED:
      return { ...state, pledgeSubmitted: false };
    case RESET_ENOUGH_PLEDGED:
      return { ...state, enoughPledged: false };

    case SET_TEMP_PLEDGE_VALUE:
      return { ...state, tempPledgeValue: action.data };
    case RESET_TEMP_PLEDGE_VALUE:
      return { ...state, tempPledgeValue: 0 };
    default:
      return state;
  }
}
