import {
  SET_PLEDGE_VALUE,
  RESET_PLEDGE_VALUE,
  UPDATE_TOTAL_PLEDGES,
  INCREASE_BACKERS,
  RESET_ENOUGH_PLEDGED,
  SET_ENOUGH_PLEDGED,
  RESET_PLEDGE_SUBMITTED,
  SET_PLEDGE_SUBMITTED,
  RESET_TEMP_PLEDGE_VALUE,
  SET_TEMP_PLEDGE_VALUE
} from "../constants";


export function setPledgeValue(pledgeValue) {
  return { type: SET_PLEDGE_VALUE, data: pledgeValue };
}

export function updateTotalPledges(pledgeValue) {
  return { type: UPDATE_TOTAL_PLEDGES, data: pledgeValue };
}

export function resetPledgeValue() {
  return { type: RESET_PLEDGE_VALUE };
}

export function increaseBackers() {
  return { type: INCREASE_BACKERS };
}


export function setEnoughPledged() {
  return { type: SET_ENOUGH_PLEDGED };
}

export function resetEnoughPledged() {
  return { type: RESET_ENOUGH_PLEDGED };
}

export function setPledgeSubmitted() {
  return { type: SET_PLEDGE_SUBMITTED };
}

export function resetPledgeSubmitted() {
  return { type: RESET_PLEDGE_SUBMITTED };
}

export function setTempPledgeValue(pledgeValue) {
  return { type: SET_TEMP_PLEDGE_VALUE, data: pledgeValue };
}

export function resetTempPledgeValue() {
  return { type: RESET_TEMP_PLEDGE_VALUE };
}

