import classes from "./PledgeArea.module.css";
import { useState } from "react";
import {
    setTempPledgeValue
  } from "../../containers/backers/actions";
  import {useDispatch } from "react-redux";
import Button from "../../components/buttons/Button";

function PledgeAreaContainer(props) {
  const [inputValue, setInputValue] = useState("");
  
  const dispatch = useDispatch();

  const handleUserInput = (e) => {
    setInputValue(e.target.value);
  };

  const resetInputFiled = () => {
    setInputValue("");
  };

  return (
   
    <div className={classes.enterPledge}>
      Enter your pledge
      <div className={classes.row}>
        <form className ={classes.form} onSubmit={props.submit}>
          <input
            className={classes.pledgeAmount}
            value={inputValue}
            onChange={handleUserInput}
            onInput={(e) => dispatch(setTempPledgeValue(e.target.value))}
            type="number"
          ></input>

          <Button
            type={props.type}
            smallButton={true}
            text="Continue"
            click={resetInputFiled}
          />
        </form>
      </div>
    </div>
  );
}

export default PledgeAreaContainer;
