import React from "react";
import classes from "./EditionsList.module.css";
import EditionContainer from ".";
import { useDispatch, useSelector } from "react-redux";
import {
  setPledgeValue,
  resetTempPledgeValue,
  setPledgeSubmitted,
  resetPledgeSubmitted,
  setEnoughPledged,
} from "../backers/actions";
import PledgeModalContainer from "../pledgeModal";
import OverlayModal from "../../components/overlay/OverlayModal";
import { EDITION_TYPE } from "./constants";

function EditionsList(props) {
  const dispatch = useDispatch();

  const state = useSelector(
    (state) => state.pledgeValues)

  const enoughPledged = state.enoughPledged
  ;
  const tempPledgeValue = state.tempPledgeValue
  ;
  const pledgeSubmitted = state.pledgeSubmitted;

  function modalHandler() {
    pledgeSubmitted
      ? dispatch(resetPledgeSubmitted())
      : dispatch(setPledgeSubmitted());
  }

  function notEnoughPledged(e) {
    e.preventDefault();
    modalHandler();
  }

  function enoughWasPledged() {
    dispatch(setPledgeValue(tempPledgeValue));
    modalHandler();
    dispatch(resetTempPledgeValue());
    dispatch(setEnoughPledged(true));
    dispatch(setPledgeSubmitted());
  }

  const commonProps = {
    tempPledgeValue: tempPledgeValue,
    enoughPledged: enoughWasPledged,
    notEnoughPledged: notEnoughPledged,
  };

  return (
    <div className={classes.container}>
      <div className={classes.column}>
        {props.modalMode ? (
          <EditionContainer
            {...commonProps}
            modalMode={props.modalMode}
            edition={EDITION_TYPE.NOREWARD}
          />
        ) : null}

        <EditionContainer
          {...commonProps}
          modalMode={props.modalMode}
          edition={EDITION_TYPE.BAMBOO}
        />

        <EditionContainer
          {...commonProps}
          modalMode={props.modalMode}
          edition={EDITION_TYPE.BLACK}
        />

        <EditionContainer
          {...commonProps}
          modalMode={props.modalMode}
          edition={EDITION_TYPE.SPECIAL}
        />
      </div>

      {pledgeSubmitted && <OverlayModal />}
      {pledgeSubmitted && enoughPledged && <PledgeModalContainer />}
      {pledgeSubmitted && !enoughPledged && <PledgeModalContainer />}
    </div>
  );
}

export default EditionsList;
