import React from "react";
import {resetPledgeSubmitted, resetEnoughPledged} from "../../containers/backers/actions";
import { useDispatch, useSelector } from "react-redux";
import data from "./data.json"
import PledgeModal from "../../components/modals/pledgeModal";

function PledgeModalContainer() {


const enoughPledged = useSelector((state)=> state.pledgeValues.enoughPledged)


  const dispatch = useDispatch()

  const close =()=>{
    dispatch(resetPledgeSubmitted())
    dispatch(resetEnoughPledged())
  }

  const message = data && data[enoughPledged]

  return (
   <PledgeModal title = {message.title} msg = {message.msg} close = {close}/>
  );
}

export default PledgeModalContainer;
