import React from "react";
import { useState, useEffect } from "react";
import Backers from "../../components/backers/Backers";
import ProgressBarMine from "../../components/progressBar/ProgressBar";
import classes from "./CurrentNumbers.module.css"
import {useSelector, useDispatch} from "react-redux"
import {updateTotalPledges, increaseBackers, resetPledgeValue } from "./actions";
import { AMOUNT_NEEDED } from "./constants";

function CurrentBackersContainer(props) {

  const [progressBar, setProgressBar] = useState(85);

  const dispatch = useDispatch()

  const state = useSelector((state)=> state.pledgeValues)

  const amtBacked = state.totalPledges
  const numOfBackers= state.numOfBackers
  const pledgeValue= state.pledgeValue
  const pledgeSubmitted = state.pledgeSubmitted

 
  function updateProgressBar(amtBacked) {
    const updatedProgress = (amtBacked / AMOUNT_NEEDED) * 100;
    setProgressBar(updatedProgress);
  }

  useEffect(() => {

    if (pledgeValue > 0) {
 
    dispatch(increaseBackers())
    dispatch(updateTotalPledges(pledgeValue))
     dispatch(resetPledgeValue())

    return (
    updateProgressBar(amtBacked + pledgeValue)
   
    )
  }

  },[pledgeSubmitted]);

  return (
    <div className={classes.currentNumbersContainer}>
    <div className = {classes.row}>
   
    
      <Backers
        number={ numOfBackers && `${numOfBackers.toLocaleString()}`}
        text={"total backers"}
      />
 
      <Backers
        number={amtBacked && `â‚¬${amtBacked.toLocaleString()}`}
        text={"of â‚¬100,000 backed"}
      />


      <Backers number={"56"} text={"days left"} />
      </div>
      <ProgressBarMine amount={progressBar} />
      </div>  
  );
}

export default CurrentBackersContainer;
