import {
  DECREASE_BAMBOO_EDITION,
  DECREASE_BLACK_EDITION,
  DECREASE_SPECIAL_EDITION,
  EDITION_TYPE
} from "../constants";

export const decreaseAmount = (edition) => {
  switch (edition) {
    case EDITION_TYPE.BAMBOO:
      return { type: DECREASE_BAMBOO_EDITION };
    case EDITION_TYPE.BLACK:
      return { type: DECREASE_BLACK_EDITION };
    case EDITION_TYPE.SPECIAL:
        return { type: DECREASE_SPECIAL_EDITION };
   default: console.log("none")
  }
};
